import React from "react";
import { Link } from "react-router-dom";

export default function Header() {
  return (
    <header>
      <nav>
        <Link to="/">
          <p className="logo">Nageeb</p>
        </Link>

        <ul>
          <Link to="/">
            <li className="active">Home</li>
          </Link>
          <a href="#skills">
            <li>Skills</li>
          </a>
          <Link to="/portfolio">
            <li>Portfolio</li>
          </Link>
        </ul>
        <a href="https://wa.me/+963941481632" target="_blank">
          <p className="contact-me">Contact Me</p>
        </a>
      </nav>
    </header>
  );
}
