import React from "react";
import leftSide from "./leftSide.json";
import rightSide from "./rightSide.json";

export default function MySkills() {
  const leftSkills = leftSide.map((item) => {
    return (
      <div key={item.id}>
        <p>{item.name}</p>
        <span style={{ width: item.prog }} number={item.prog}></span>
      </div>
    );
  });
  const rightSkills = rightSide.map((item) => {
    return (
      <div key={item.id}>
        <p>{item.name}</p>
        <span style={{ width: item.prog }} number={item.prog}></span>
      </div>
    );
  });

  return (
    <section id="skills">
      <div className="skills">
        <h1>My Skills</h1>
        <div className="my-skills">
          <div className="left-side">{leftSkills}</div>
          <div className="right-side">{rightSkills}</div>
        </div>
      </div>
    </section>
  );
}
